:root {
  /* Color Pallete
     https://coolors.co/011627-fdfffc-2ec4b6-e71d36-ff9f1c
  */
  --color11: #011627; /* MAASTRICHT BLUE */
  --color21: #fdfffc; /* BABY POWDER */
  --color31: #2ec4b6; /* MAXIMUM BLUE GREEN */
  --color41: #e71d36; /* ROSE MADDER */
  --color51: #ff9f1c; /* BRIGHT YELLOW (CRAYOLA) */

  /* Alternative Colors */
  --color12: #465561; /* INDEPENDENCE */
  --color22: #fdfffc; /* BABY POWDER */
  --color32: #67d4c9; /* MEDIUM TURQUOISE */
  --color42: #ed5a6c; /* LIGHT CARMINE PINK */
  --color52: #ffb959; /* PASTEL ORANGE */

  /* linear-gradient arguments  */
  --direction: 110deg;
  /* --pantone: #171796; */

  /* CARD */
  --bg-spring-first-direction: 70deg;
  --bg-spring-second-direction: 30deg;
}

.size {
  /*
  NOTE: If we set the position to be relative,
        the predicted object box will be out of
        the image.
  */
  position: absolute;
  top: 20%;
  left: 15%;
  width: 70%;
  height: 70%;
}

body {
  font-size: 15px;
  background: var(--color22);
}

/* CARD
taken from https://blog.prototypr.io/css-only-multi-color-backgrounds-4d96a5569a20
*/
.card-holder {
  margin: 2em 0;
}

.card {
  color: var(--color11);
  font-family: -apple-system, BlinkMacSystemFont, "Open Sans", sans-serif;
  font-size: 3em;
  font-weight: 800;
  height: 1em;
  width: 64em;
  padding: 0.5em 1em;
  border-radius: 0.25em;
  display: table-cell;
  vertical-align: middle;
  letter-spacing: -2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.card .subtle {
  color: #000;
  font-size: 0.5em;
  font-weight: 400;
  letter-spacing: -1px;
}

.card i {
  font-size: 3em;
}

.bg-spring {
  background: -webkit-linear-gradient(
      var(--bg-spring-first-direction),
      var(--color52) 30%,
      rgba(0, 0, 0, 0) 30%
    ),
    -webkit-linear-gradient(var(--bg-spring-second-direction), var(--color32)
          60%, var(--color42) 60%);

  background: -o-linear-gradient(
      var(--bg-spring-first-direction),
      var(--color52) 30%,
      rgba(0, 0, 0, 0) 30%
    ),
    -o-linear-gradient(var(--bg-spring-second-direction), var(--color32) 60%, var(
            --color42
          )
          60%);

  background: -moz-linear-gradient(
      var(--bg-spring-first-direction),
      var(--color52) 30%,
      rgba(0, 0, 0, 0) 30%
    ),
    -moz-linear-gradient(var(--bg-spring-second-direction), var(--color32) 60%, var(
            --color42
          )
          60%);

  background: linear-gradient(
      var(--bg-spring-first-direction),
      var(--color52) 30%,
      rgba(0, 0, 0, 0) 30%
    ),
    linear-gradient(
      var(--bg-spring-second-direction),
      var(--color32) 60%,
      var(--color42) 60%
    );
}
